.MainContainer {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding-top: 200px;
    box-sizing: border-box;
}

.SubContainer {
    min-width: 40%;
    box-shadow: 0 0 18px rgba(180, 180, 180,0.5);
    height: 100%;
}

.FormSelector {
    width: 100%;
    box-sizing: border-box;
    padding: 8px 0;
}

.FormNames {
    cursor: pointer;
    padding: 0 12px;
    font-size: 1.5em;
    letter-spacing: 0.5px;
    font-weight: 500;
}

.FormContainer {
    width: 100%;
}

