.MainContainer {
    display: flex;
    padding: 24px 7%;
    box-sizing: border-box;
}

.LeftSection {
    display: flex;
    flex-direction: column;
    width: 15%;
}

.FilterMenuContainers {
    display: flex;
    flex-direction: column;
}

.FilterMenus {
    margin: 4px 0;
    font-size: 18px;
}

.Filters {
    margin-right: 8px;
}

.RightSection {
    width: 85%;
}

.FilterContainer {
    display: flex;
    justify-content: left;
    align-items: center;
    margin: 4px 0;
    width: fit-content;
    cursor: pointer;
    opacity: 0.8;
}

.FilterContainer:hover {
    opacity: 1;
}

